body, html, #root, .App {
  height: 100%;
  margin: 0;
  font-family: Arial, sans-serif;
}

header, footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 1rem 0;
}

aside {
  background-color: #f4f4f4;
  padding: 1rem;
  width: 200px;
  float: left;
  height: calc(100% - 2rem); /* Header와 Footer의 높이만큼 빼줍니다. */
  box-sizing: border-box;
}

main {
  margin-left: 200px;
  padding: 1rem;
}

article {
  background-color: #fff;
  border: 1px solid #ddd;
  padding: 1rem;
  margin-bottom: 1rem;
}

p {
  line-height: 1.5;
}
